body {
  overflow: hidden; /* Prevent scrolling */
  margin: 0;
}

/* General layout for the cooking section */
.cooking {
  padding: 20px;
  font-family: 'Arial', sans-serif;
}

.cooking h1, .cooking h2 {
  text-align: center;
  color: #1c2833; /* Darker navy color for headings */
}

/* Container for all recipes */
.recipes {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px; /* Space between cards */
}

/* Individual recipe card */
.recipe {
  background: #e3f2fd; /* Light blue background */
  margin: 10px;
  padding: 10px; /* Compact padding */
  border-radius: 10px;
  width: 570px; /* Larger width for the recipe card */
  height: 730px; /* Slightly taller height to show more content */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  overflow: hidden; /* Prevent content from overflowing */
  transition: box-shadow 0.3s ease;
}

/* Image styling inside the recipe card */
.recipe img {
  max-width: 100%;
  max-height: 200px; /* Restrict image height */
  border-radius: 10px;
  margin-bottom: 8px;
  object-fit: cover; /* Ensure images stay visually appealing */
}

/* Recipe title styling */
.recipe h3 {
  font-size: 1rem; /* Smaller heading size */
  margin-bottom: 10px;
  color: #1c2833; /* Darker navy color for titles */
  overflow: hidden; /* Ensure title doesn't overflow */
  text-overflow: ellipsis; /* Add ellipsis if title is too long */
}

/* Recipe description styling */
.recipe p {
  font-size: 0.875rem; /* Smaller description text */
  line-height: 1.2; /* Tighter line spacing */
  color: #1c2833; /* Darker navy color for text */
  overflow: hidden; /* Prevent description from overflowing */
  text-overflow: ellipsis; /* Add ellipsis if text is too long */
}

/* Remove hover effects for the recipe card */
.recipe:hover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}
